<template>
	<!-- Main Area -->
	<div class="main-area">
		<section class="album-section">
			<div class="album-section-title">
				<div class="bg-overly" :style="{ backgroundImage: `url('${artists.picture}')` }"></div>
				<div class="single-track-pic" :style="{ backgroundImage: `url('${artists.picture}')` }"></div>
				<h3>{{artists.name}}</h3>
			</div>
			<div class="album-items">
				<div class="album-item" v-for="(album, index) in artists.albums" :index="index" :key="index">
					<router-link :to="{ name: 'Album', params: { slug: album.slug }}">
						<div class="album-poster" :style="{ backgroundImage: `url('${album.picture}')` }"></div>
						<div class="album-footer">
							<h4>{{album.title}}</h4>
							<span v-for="(artist, index) in album.artists" :key="index">{{artist}}</span>
						</div>
					</router-link>
				</div>
			</div>
		</section>
	</div>
</template>

<script>
const axios = require('axios');
export default {
	name: 'Artist',
	data: function(){
		return{
			artists: []
		}
	},
	mounted(){
		axios.get('/api/artists/'+ this.$route.params.slug +'/?format=json').then(response => (this.artists = response.data))
	},
	watch:{
		$route (){
			axios.get('/api/artists/' + this.$route.params.slug + '/?format=json').then(response => (this.artists = response.data))
		}
	}
}
</script>
<style scoped lang="scss">
.single-track-pic{
	width: 85px;
	height: 85px;
	background-position: center;
	background-size: cover;
	margin-right: 15px;
	border-radius: 4px;
}
.album-section-title {
    padding: 30px 0;
}
</style>